.map--home .map__container {
  position: relative;

  background-color: $light;
}

.map__contact {
  position: relative;

  display: grid;
  padding: 26px 0;

  grid-template-columns: repeat(2, 1fr);

  @media (min-width: $tablet-width) {
    padding: 61px 40px;

    gap: 308px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;

    width: 565px;
    margin: 95px 29px;
    padding: 61px 80px;

    background-color: $light;

    gap: 87px;
  }
}

.map__title {
  margin: 0;

  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 26px;
  }
}

.map__adreess {
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  font-family: $ff-lato;
  color: $dark-contet;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 20px;
  }
}

.map__adreess span {
  display: block;
  margin-top: 19px;
}

.map__iframe-container {
  height: 400px;
}

.map__iframe {
  height: 100%;
}

.map__iframe iframe {
  display: block;
  width: 100%;
  height: 100%;

  border: none;
}
